import React, { Component } from "react";

import "../stylesheet/default.css";

class BubbleSocial extends Component {
	render() {
		return (
			<div className="bubble-social">
				<a href={this.props.url}>
					<img src={this.props.logo} alt={this.props.title} />
				</a>
			</div>
		);
	}
}

export default BubbleSocial;
