import React, { Component } from "react";
import { NavLink } from "react-router-dom";

import "../stylesheet/default.css";

class Header extends Component {
	render() {
		return (
			<div className="header">
				<div>
					<span id="KVIN">KVIN</span>
					<span id="TANAKA">TANAKA</span>
				</div>
				<div id="SOFTWARE-DEVELOPER">
					<span>SOFTWARE DEVELOPER</span>
				</div>
				<nav id="DESKTOP">
					<NavLink id="HOME" exact to="/">
						HOME
					</NavLink>
					<NavLink id="CURRENT-PROJECTS" to="/current-projects">
						CURRENT PROJECTS
					</NavLink>
					<NavLink id="ARTICLES" to="/articles">
						ARTICLES
					</NavLink>
				</nav>
				<nav id="MOBILE"></nav>
			</div>
		);
	}
}

export default Header;
