import React, { Component } from "react";

import BubbleBox from "../components/BubbleBox";

class Articles extends Component {
	constructor() {
		super();
		this.articles = [];
		this.rendered = [];

		this.fetchArticles();
	}

	fetchArticles() {
		const api =
			"https://wordpress.kvintanaka.com/wp-json/wp/v2/posts/?filter[posts_per_page]=5";
		fetch(api)
			.then((response) => response.json())
			.then((articles) =>
				articles.forEach((article) =>
					this.articles.push({
						title: article.title.rendered,
						url: article.link,
						content: article.content.rendered,
					})
				)
			)
			.then(() => this.buildRendered())
			.then(() => this.forceUpdate());
	}

	buildRendered() {
		this.articles.forEach((article) => {
			this.rendered.push(
				<BubbleBox>
					<div className="articles">
						<img
							src="https://upload.wikimedia.org/wikipedia/commons/9/98/WordPress_blue_logo.svg"
							alt="Wordpress article"
							width="50px"
							height="50px"
						/>
						<a href={article.url}>{article.title}</a>
					</div>
				</BubbleBox>
			);
		});
	}

	render() {
		return <div className="articles">{this.rendered}</div>;
	}
}

export default Articles;
