import React, { Component } from "react";

import "../stylesheet/default.css";

class BubbleButton extends Component {
	render() {
		return (
			<div className="bubble-button">
				<a id={this.props.id} href={this.props.url}>
					{this.props.title}
				</a>
			</div>
		);
	}
}

export default BubbleButton;
