import React from "react";

import BubbleBox from "../components/BubbleBox";
import BubbleButton from "../components/BubbleButton";
import BubbleSocial from "../components/BubbleSocial";

function Home() {
	return (
		<div>
			<BubbleBox>
				<div className="quote">
					<p id="SMALL">
						“Let’s Build. Bring technologies to improve our live. We’re proud
						Software Engineer and together we bring innovation in software ,
						especially the one that can be enjoyed by a lot of people.”
					</p>
					<p id="BIG">
						“I believe together we can, everyone have their own strength and
						combined we will surely achieve a greater result.”
					</p>
					<BubbleButton
						id="COLLABORATE"
						title="Let’s Collaborate"
						url="https://github.com/kvintanaka"
					/>
				</div>
			</BubbleBox>
			<BubbleSocial
				title="Github"
				url="https://github.com/kvintanaka"
				logo="https://upload.wikimedia.org/wikipedia/commons/9/91/Octicons-mark-github.svg"
			/>
		</div>
	);
}

export default Home;
